import React from "react";
import { connect } from "react-redux";
import ImgCard from "./ImgCard";
import { MainHeader } from "./styles";
import { Route } from "react-router-dom";
import Modal from "./Modal";
import styled from "styled-components";
import Lottie from "react-lottie";
import animationData from "../lottie/circle.json";

const Container = styled.div`
  /* @media only screen and (max-width: 768px) { */
  flex-direction: row;
  /* } */
  display: flex;
  flex-wrap: wrap;
`;

const CardContainer = styled.div`
  max-width: 500px;
  @media only screen and (min-width: 768px) {
    margin: 1rem 3rem;
  }
`;

const Place = ({ history, match, photos }) => {
  const { place } = match.params;
  document.getElementsByTagName("body")[0].style.overflow = "";
  document.getElementsByTagName("body")[0].style.height = "";
  return (
    <div style={{ display: "flex", flexDirection: "column" }}>
      <Route
        path={`${match.url}/view/:pic`}
        render={({ match }) => {
          const pic = photos.filter(
            p => p.id === parseInt(match.params.pic)
          )[0];
          document.getElementsByTagName("body")[0].style.overflow = "hidden";
          document.getElementsByTagName("body")[0].style.height = "100%";
          return (
            <Modal
              onClick={() => {
                history.goBack();
              }}
            >
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  height: "100%"
                }}
              >
                <Lottie
                  height={400}
                  width={400}
                  options={{ loop: true, autoplay: true, animationData }}
                />
                {pic && (
                  <img
                    width="100%"
                    src={pic.url}
                    onLoad={e => {
                      console.log(e.target.height);
                    }}
                    onError={() => console.log("img error")}
                  />
                )}
              </div>
            </Modal>
          );
        }}
      />
      <MainHeader>{place}</MainHeader>
      <Container>
        {photos.map(p => {
          return (
            <CardContainer
              key={p.url}
              to={`${match.url}/chicken/${p.id}`}
              onClick={() => {
                console.log("ASDASDASD");
                history.push(`${match.url}/view/${p.id}`);
              }}
            >
              <ImgCard
                id={p.id}
                url={p.url}
                title={p.title}
                description={p.description}
              />
            </CardContainer>
          );
        })}
      </Container>
    </div>
  );
};

const mapState = state => {
  const {
    view: { photos }
  } = state;
  return { photos };
};

export default connect(mapState)(Place);
